import VueRouter from 'vue-router'

//
import Index from '../pages'
import Home from '../pages/home/home'
import Login from '../pages/login/login'
import Register from '../pages/register/register'
import Center from '../pages/center/center'
import Messages from '../pages/messages/list'
import Storeup from '../pages/storeup/list'
import AddrList from '../pages/shop-address/list'
import AddrAdd from '../pages/shop-address/addOrUpdate'
import Order from '../pages/shop-order/list'
import OrderConfirm from '../pages/shop-order/confirm'
import Cart from '../pages/shop-cart/list'
import News from '../pages/news/news-list'
import NewsDetail from '../pages/news/news-detail'
import payList from '../pages/pay'

import meishiList from '../pages/meishi/list'
import meishiDetail from '../pages/meishi/detail'
import meishiAdd from '../pages/meishi/add'
import yonghuList from '../pages/yonghu/list'
import yonghuDetail from '../pages/yonghu/detail'
import yonghuAdd from '../pages/yonghu/add'
import shangjiaList from '../pages/shangjia/list'
import shangjiaDetail from '../pages/shangjia/detail'
import shangjiaAdd from '../pages/shangjia/add'
import caipinleixingList from '../pages/caipinleixing/list'
import caipinleixingDetail from '../pages/caipinleixing/detail'
import caipinleixingAdd from '../pages/caipinleixing/add'
import caipinxinxiList from '../pages/caipinxinxi/list'
import caipinxinxiDetail from '../pages/caipinxinxi/detail'
import caipinxinxiAdd from '../pages/caipinxinxi/add'
import newstypeList from '../pages/newstype/list'
import newstypeDetail from '../pages/newstype/detail'
import newstypeAdd from '../pages/newstype/add'
import systemintroList from '../pages/systemintro/list'
import systemintroDetail from '../pages/systemintro/detail'
import systemintroAdd from '../pages/systemintro/add'
import discusscaipinxinxiList from '../pages/discusscaipinxinxi/list'
import discusscaipinxinxiDetail from '../pages/discusscaipinxinxi/detail'
import discusscaipinxinxiAdd from '../pages/discusscaipinxinxi/add'

const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}

//
export default new VueRouter({
	routes:[
		{
      path: '/',
      redirect: '/index/home'
    },
		{
			path: '/index',
			component: Index,
			children:[
				{
					path: 'home',
					component: Home
				},
				{
					path: 'center',
					component: Center,
				},
				{
					path: 'pay',
					component: payList,
				},
				{
					path: 'messages',
					component: Messages
				},
				{
					path: 'storeup',
					component: Storeup
				},
                {
                    path: 'shop-address/list',
                    component: AddrList
                },
                {
                    path: 'shop-address/addOrUpdate',
                    component: AddrAdd
                },
				{
					path: 'shop-order/order',
					component: Order
				},
				{
					path: 'cart',
					component: Cart
				},
				{
					path: 'shop-order/orderConfirm',
					component: OrderConfirm
				},
				{
					path: 'news',
					component: News
				},
				{
					path: 'newsDetail',
					component: NewsDetail
				},
				{
					path: 'meishi',
					component: meishiList
				},
				{
					path: 'meishiDetail',
					component: meishiDetail
				},
				{
					path: 'meishiAdd',
					component: meishiAdd
				},
				{
					path: 'yonghu',
					component: yonghuList
				},
				{
					path: 'yonghuDetail',
					component: yonghuDetail
				},
				{
					path: 'yonghuAdd',
					component: yonghuAdd
				},
				{
					path: 'shangjia',
					component: shangjiaList
				},
				{
					path: 'shangjiaDetail',
					component: shangjiaDetail
				},
				{
					path: 'shangjiaAdd',
					component: shangjiaAdd
				},
				{
					path: 'caipinleixing',
					component: caipinleixingList
				},
				{
					path: 'caipinleixingDetail',
					component: caipinleixingDetail
				},
				{
					path: 'caipinleixingAdd',
					component: caipinleixingAdd
				},
				{
					path: 'caipinxinxi',
					component: caipinxinxiList
				},
				{
					path: 'caipinxinxiDetail',
					component: caipinxinxiDetail
				},
				{
					path: 'caipinxinxiAdd',
					component: caipinxinxiAdd
				},
				{
					path: 'newstype',
					component: newstypeList
				},
				{
					path: 'newstypeDetail',
					component: newstypeDetail
				},
				{
					path: 'newstypeAdd',
					component: newstypeAdd
				},
				{
					path: 'systemintro',
					component: systemintroList
				},
				{
					path: 'systemintroDetail',
					component: systemintroDetail
				},
				{
					path: 'systemintroAdd',
					component: systemintroAdd
				},
				{
					path: 'discusscaipinxinxi',
					component: discusscaipinxinxiList
				},
				{
					path: 'discusscaipinxinxiDetail',
					component: discusscaipinxinxiDetail
				},
				{
					path: 'discusscaipinxinxiAdd',
					component: discusscaipinxinxiAdd
				},
			]
		},
		{
			path: '/login',
			component: Login
		},
		{
			path: '/register',
			component: Register
		},
	]
})
